.profile {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  height: auto;
  width: 400px;
  background-color: lightgray;
  text-align: center;
  margin-top: 40px;
  padding: 40px;
  border: 1px gray;
  border-radius: 10px;
  text-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  -moz-box-shadow: 8px 8px 18px 0px rgba(66, 68, 90, 1);
  box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
}
.descriptions {
  background-color: antiquewhite;
  border-radius: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  line-height: 4px;
  width: auto;
}
.location {
  font-size: 22px;
  color: gray;
}
.avatar {
  width: 220px;
  padding: 40px;
}
.name {
  font-weight: 800;
}
.tag {
  font-size: 22px;
  color: gray;
}
.stats {
  padding: 0;
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: space-between;
  width: auto;
  padding: 15px;
  border: 3px solid whitesmoke;
  border-radius: 10px;
}
.item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  color: rgb(13, 95, 122);
  font-weight: 400;
  margin-top: 20px;
}
.label {
}
.quantity {
  margin-top: 10px;
  font-weight: 800;
}
