.statistics {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  height: auto;
  width: 613px;
  background-color: lightgray;
  text-align: center;
  margin-top: 40px;
  padding: 40px;
  border: 1px gray;
  border-radius: 10px;

  text-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  -moz-box-shadow: 8px 8px 18px 0px rgba(66, 68, 90, 1);
  box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
}
.title {
  background-color: antiquewhite;
  border-radius: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  height: 80px;
  width: auto;
  font-weight: 800;
  padding-top: 30px;
}
.statList {
  font-size: 25px;
  color: whitesmoke;
  padding: 0;
  margin: 0px;
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: space-between;
  width: 400px;
}
.item {
  border: 3px solid whitesmoke;
  border-radius: 10px;
  margin: 2px;
}
